















import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import wx from 'weixin-js-sdk'
import { formatDate } from '@/until/index'

@Component({
  components: {

  },
  filters: {
   
  }
})
export default class houseSelect extends Vue {

  houseList = []
  houseId = ''
  activated () {
    let query = this.$route.query
    let state = query.state
    let houseId = ''
    if (state) {
      let stateArr = state.split('@')
      if (stateArr[0] === 'houseId') {
        houseId = stateArr[1]
        query.houseId = houseId
      }
    }
    if (this.houseId && query.houseId && query.houseId === this.houseId) {
      this.$emit('change', this.houseId)
    } else if (this.houseId && !query.houseId) {
      return
    } else {
      this.pageInit()
    }
  }

  // 计算属性
  get computedMsg () {
    return 'computed ' + this.propB
  }
  get houseOptions () {
    this.houseList.forEach((obj) =>{
      obj.value = obj.id
      obj.text = obj.account_info[0].name + ' ' + obj.build_info[0].name + ' '+ obj.unitNum + '单元' + obj.floorNum + '层' + obj.roomNum + '室'
    })
    return this.houseList
  }
  getBindingInfo () {
    let query = this.$route.query
    if (query.code) {
      const toast = this['$createToast']({
        time: 0,
        txt: '数据获取中...'
      })
      toast.show()
      this.$store.dispatch('getCodeInfo', { code: query.code }).then((response) => {
        if (response.success) {
          if (response.data.openid) {
            this.openid = response.data.openid
            this.$router.replace({ path: '/login', query: { openid: response.data.openid, jump: '/home' } })
          } else if (response.data.sessionId) {
            this.$store.commit('SET_SESSION_ID', response.data.sessionId)
            this.$store.commit('SET_USER_INFO', response.data.userInfo)
            if (response.data.userInfo.houseId && response.data.userInfo.houseId.length > 0) {
              this.getHouseList()
            }
          }
        }
        toast.hide()
      })
    } else {
      console.log('不是微信登陆')
    }
  }
  pageInit () {
    let query = this.$route.query
    if (this.$store.state.houseList && this.$store.state.houseList.length > 0) {
      this.$store.state.houseList.forEach((item, index) => {
        this.$set(this.houseList, index, item)
      })
      if (query.houseId) {
        this.houseId = query.houseId
      } else {
        this.houseId = this.houseList[0].id
      }
      return
    }
    if (this.$store.state.sessionId && !this.houseId) {
      this.getHouseList()
    } else if (query.code && !this.$store.state.sessionId) {
      this.getBindingInfo()
    }
  }

  getHouseList () {
    let that = this
    this.$store.dispatch('getHouseList', {}).then((response) => {
      if (response.data.list) {
        response.data.list.forEach((item, index) => {
          that.$set(that.houseList, index, item)
        })
        if (!this.houseId) {
          this.houseId = this.houseList[0].id
        }
        this.$store.commit('SET_HOUSE_LIST', this.houseList)
      }
    })
  }
  getFloorByAccountId () {
    const toast = this['$createToast']({
      time: 0,
      txt: '数据获取中...'
    })
    toast.show()
    this.$store.dispatch('getFloorByAccountId', { accountId: this.accountId }).then((response) => {
      if (response.success) {
        if (this.selectedLabelDefault === 'incomplete') {
          this.billList = response.data.list
          this.initCheckbox()
        } else {
          if (response.data.list.length > 0) {
            this.billList = this.billList.concat(response.data.list)
          } else {
            this.isShowMore = false
          }
        }
      }
      toast.hide()
    })
  }

  @Watch('houseId')
  houseChange (id: any) {
    this.$emit('change', id)
  }
}
